.set-assign-con {
	.box-container {
		@include mobile {
			margin-right: 0;
		}
		.box-button {
			cursor: initial;
			.box-name {
				cursor: pointer;
				line-height: 140%;
			}
		}
	}
}

.setting-as-row {
    width: 100%;
    & > div {
        width: 48%;
        margin: 0 1% 0 1%;
    }
}

.set-assign-column {
    & > * {
        margin-bottom: 1rem !important;
    }
}