#page-con{
	display: flex;
	height: 100vh;
	&>div{
		overflow-y: scroll;
		&::-webkit-scrollbar {
			display: none;
		}
	}
	#wrap{
		display: flex;
		flex-direction: column;
		flex-shrink: 1;
		width: 100%;
		.back-margin{
			z-index: -1;
		}
		.content-wrap{
			padding: 1rem 5rem;
			@include mobile {
				width: 95vw;
				margin: 0 auto;
			}
		}
	}
	.mobile-check{
		width: 100vw;
		position: fixed;
	}
	#badge{
		position: fixed;
		bottom: 0;
		right: 0;
		background-color: white;
		padding: 0.6rem;
		border-radius: 2rem;
		border: 1px solid lightgray;
		margin: 1.5rem 2rem;
		cursor: pointer;
		z-index: 1;
	}
	#alert{
		position: absolute;
		bottom: 0;
		right: 0;
		margin: 1.5rem 2rem;
		z-index: 2;
		max-width: 280px;
		svg{
			cursor: pointer;
		}
		.title{
			display: flex;
			justify-content: space-between;
			align-items: center;
			svg{
				margin-left: 1rem;
			}
		}
		.info{
			font-weight: 600;
		}
		.sub-info{
			font-size: 0.8rem;
			border-radius: 1rem;
			padding: 5px 10px;
			color: $navy;
			background-color: white;
		}
	}
}