
.Login{
    overflow: auto;
    width:100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #FFF1ED;
}

.login_paper {
    max-width: 500px;
    margin: auto;
    padding: 1rem 3rem;
}

.login_logo {
    padding: 2rem 0 1rem;
}
.login_logo img{
    width: 220px;
    @include mobile {
        width: 200px;
    }
}

.login_subtitle {
    padding: 0 0 1.3rem 0;
}

.login_sign {
    display: flex;
    justify-items: center;
    align-items: center;
}

.login_sign>a {
    cursor: pointer;
    font-weight: bold;
    margin-right: 5px;
}

.login__form {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.regiter_sign {
    padding: 0.5rem 0 0 0;
}

.login_divider {
    width: 80%;
    padding-bottom: 1rem;
}

.login_container{
    h6{
        text-align: center;
    }
    &> form >div{
        margin: 10px;
    }
}

.login_button {
    width: 60%;
    margin: 0 0 1rem 0 !important;
}

.login_test {
    margin: 0.25rem 0.25rem 2rem 0.25rem !important;
}
 
/* button */
.add_button{
    background: rgb(204, 204, 204)!important;
}
.add_button:hover{
    background: rgb(180, 180, 180)!important;
}
.x_button{    
    background: gainsboro!important;
}
.x_button:hover{
    background: rgb(204, 204, 204)!important;
}
.add_button, .x_button{
    width: 130px;
    text-align: center;
}
.add_button p, .x_button p{
    padding: 10px 0;
}
.save_button{
    background-color: rgb(180, 180, 180)!important;
}
.add_button button{
    width: 100%;
    padding: 0;
}
