.admin-form-con{
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100%;
	form{
		padding: 2rem;
		border-radius: 1rem;
		border: 1px solid lightgray;
		display: flex;
		flex-direction: column;
		align-items: center;
		&>div:first-of-type{
			margin-bottom: 1rem;
		}
	}
}